import { SpaceProps, BorderProps } from '@sieloapp/shared/styled';
import { Container } from '@sieloapp/components';
import { StyledContent } from './style';

interface IProps extends SpaceProps, BorderProps {
  children: React.ReactNode;
  className?: string;
  fullHeight?: boolean;
  flex?: boolean;
  align?: 'top' | 'center' | 'bottom';
}

const Content = ({
  children,
  className,
  fullHeight,
  flex,
  align,
  ...restProps
}: IProps) => {
  return (
    <StyledContent
      $fullHeight={fullHeight}
      $flex={flex}
      $align={align}
      className={className}
      {...restProps}
    >
      <Container className="container" pl="0" pr="0">
        {children}
      </Container>
    </StyledContent>
  );
};

export default Content;
